@custom-header-color: #ec407a;
.header{
  margin: 0 auto;
  background-color: #f06292;
  color: #fff;
}

.header a{
  color: #fff;
}

.header a:hover{
  text-decoration: none;
}

.header-big{
  padding: 20px;
}

.header-small{
  padding: 7px 10px;
  font-size: 0.86em;
}

.header-icons{

}

.header-icon{
  margin-left: 15px;
}

.slogan{
  font-family: @font-family-hand-written;
}

.circle{
  width: 36px;
  height: 36px;
  display: inline-block;
  -moz-border-radius: 60%;
  -webkit-border-radius: 60%;
  border-radius: 100%;
  -moz-box-shadow: 0px 0px 2px #000;
  -webkit-box-shadow: 0px 0px 2px #000;
  box-shadow: 0px 0px 2px #000;
  padding: 5px;
}

.action{
  color: @navbar-default-color;
}

p{
  font-size: 1.1em !important;
}

.block-flat {
  margin-bottom: 40px;
  padding: 20px 20px;
  background: #fff;
  border-bottom: 1px solid #e2e2e2;
  box-shadow: 1px 1px 1px 0px rgba(0, 0, 0, 0.08);
}

.shadow{
  border-left: 1px solid #efefef;
  border-right: 1px solid #efefef;
  border-bottom: 1px solid #e2e2e2;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.04);
}

.margins > a{
  margin: 0 5px;
}

.cursor{
  cursor: pointer;
}
.box {
  border-radius: 0px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  padding: 10px 25px;
  text-align: right;
  display: block;
  margin-top: 60px;
}
.box-icon {
  border-radius: 50%;
  display: table;
  height: 100px;
  margin: 0 auto;
  width: 100px;
  margin-top: -61px;
}
.box-icon span {
  color: #fff;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
.info h4 {
  font-size: 26px;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.info > p {
  color: #717171;
  font-size: 16px;
  padding-top: 10px;
  text-align: justify;
}
.info > a {
  background-color: #03a9f4;
  border-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  color: #fff;
  transition: all 0.5s ease 0s;
}